<template>
  <div id="base-search">
    <div class="text">
      <span></span>
      <span class="selected"></span>
    </div>
    <el-select v-model="field" placeholder="" size="small" style="width:160px">
      <el-option
        v-for="item in fields"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>
    <el-select v-model="condition" placeholder="" size="small" style="width:80px">
      <el-option
        v-for="item in conditions"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>
    <el-input placeholder="" v-model="value" size="small" style="width:160px"></el-input>
    <el-select v-model="and" placeholder="" size="small" style="width:80px">
      <el-option
        v-for="item in ands"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>
    <i class="el-icon-d-arrow-left add-btn" @click="showMoreCondition"></i>
    <el-button type="primary" size="small" style="width:100px;"></el-button>
    <el-button type="primary" size="small" style="width:100px;"></el-button>
    <div class="condition-action">
      <span @click="isShowSearchModal = true"></span>
      <span></span>
    </div>
    <div class="more-condition" v-show="isShowMoreCondition">
      <el-button type="primary" size="small" icon="el-icon-plus"></el-button>
    </div>
    <el-dialog
      title=""
      :visible.sync="isShowSearchModal"
      width="800px">
      <div class="wrapper">
        <div class="search-date">
          <el-button size="small"></el-button>
          <el-button type="primary" size="small"></el-button>
          <el-button size="small"></el-button>
          <el-button size="small"></el-button>
          <el-button size="small"></el-button>
        </div>
        <div class="search-content">
          <div class="save-name">
            
            <el-input
              placeholder=""
              v-model="value"
              size="small"
              style="width:160px">
            </el-input>
            <el-checkbox></el-checkbox>
          </div>
          <el-tabs v-model="activeName2" type="card" class="tabs" size="small">
            <el-tab-pane label="" name="first">
              <el-table
                class="table-condition"
                :data="tableConditionData"
                style="width:600px"
                border
                size="small">
                <el-table-column
                  prop="date"
                  label="">
                </el-table-column>
                <el-table-column
                  prop="date"
                  label="">
                </el-table-column>
                <el-table-column
                  prop="name"
                  label="">
                </el-table-column>
                <el-table-column
                  prop="address"
                  label="">
                </el-table-column>
                <el-table-column
                  prop="address"
                  label="">
                </el-table-column>
              </el-table>
            </el-tab-pane>
            <el-tab-pane label="" name="second">
              <el-table
                class="table-condition"
                :data="tableConditionData"
                style="width:600px"
                border
                size="small">
                <el-table-column
                  prop="date"
                  label="">
                </el-table-column>
                <el-table-column
                  prop="date"
                  label="">
                </el-table-column>
                <el-table-column
                  prop="name"
                  label="">
                </el-table-column>
                <el-table-column
                  prop="address"
                  label="">
                </el-table-column>
                <el-table-column
                  prop="address"
                  label="">
                </el-table-column>
              </el-table>
            </el-tab-pane>
            <el-tab-pane label="" name="third">
              <el-table
                class="table-condition"
                :data="tableConditionData"
                style="width:600px"
                border
                size="small">
                <el-table-column
                  prop="date"
                  label="">
                </el-table-column>
                <el-table-column
                  prop="date"
                  label="">
                </el-table-column>
                <el-table-column
                  prop="name"
                  label="">
                </el-table-column>
                <el-table-column
                  prop="address"
                  label="">
                </el-table-column>
                <el-table-column
                  prop="address"
                  label="">
                </el-table-column>
              </el-table>
            </el-tab-pane>
          </el-tabs>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      fields: [{
        value: '1',
        label: '',
      }, {
        value: '2',
        label: '',
      }, {
        value: '3',
        label: '',
      }, {
        value: '4',
        label: '',
      }, {
        value: '5',
        label: '',
      }],
      conditions: [{
        value: '',
        label: '',
      }, {
        value: '',
        label: '',
      }, {
        value: '',
        label: '',
      }, {
        value: '',
        label: '',
      }, {
        value: '',
        label: '',
      }, {
        value: '',
        label: '',
      }, {
        value: '',
        label: '',
      }, {
        value: '',
        label: '',
      }, {
        value: 'in',
        label: 'in',
      }, {
        value: '',
        label: '',
      }, {
        value: '',
        label: '',
      }],
      ands: [{
        value: '',
        label: '',
      }, {
        value: '',
        label: '',
      }],
      field: '',
      condition: '',
      value: '',
      and: '',
      isShowMoreCondition: false,
      isShowSearchModal: false,
      activeName2: 'first',
      tableConditionData: [],
    };
  },
  methods: {
    showMoreCondition() {
      this.isShowMoreCondition = !this.isShowMoreCondition;
    },
  },
};
</script>

<style lang="less" scoped>
  #base-search {
    position: relative;
    .text {
      padding: 10px 0 10px 0;
      font-weight: 600;
      cursor: pointer;
      .selected {
        color: #0b24fb;
      }
      span {
        &:hover {
          color: #000;
        }
      }
    }
    .add-btn {
      vertical-align: middle;
      font-size: 24px;
      transition: all .2s;
      transform: rotate(-90deg);
      color: #409EFF;
      cursor: pointer;
    }
    .condition-action {
      height: 32px;
      display: inline-block;
      margin-left: 4px;
      font-size: 12px;
      color: #0b24fb;
      font-weight: 600;
      cursor: pointer;
      span {
          &:hover {
          color: #000;
        }
      }
    }
    .more-condition {
      position: absolute;
      top: 80px;
      left: 0;
      width: 470px;
      padding: 10px;
      border: 1px solid #dcdfe6;
      border-radius: 4px;
    }
    .wrapper {
      display: flex;
      flex-direction: row;
      .search-date {
        width: 140px;
        height: 400px;
        border: 1px solid #f2f2f2;
        display: flex;
        flex-direction: column;
        align-items: center;
        .el-button {
          margin: 4px auto;
          width: 120px;
          height: 26px;
          padding: 0 0;
          font-size: 11px;
        }
      }
      .search-content {
        flex: 1;
        display: flex;
        flex-direction: column;
        border: 1px solid #f2f2f2;
        padding: 10px;
        margin-left: 10px;
        .save-name {
          width: 60%;
          height: 32px;
          font-size: 12px;
          font-weight: 500;
          display: flex;
          flex-direction: row;
          align-items: center;
          .el-checkbox {
            margin-left: 20px;
          }
        }
        .tabs {
          margin-top: 20px;
        }
      }
    }
  }
</style>



